.program-service-card {
    background-color: #f7f5ff !important;
    box-shadow: 0 0 16px rgba(0, 0, 0, 0.2);
}

.service-card-1 {
    background: var(--color-white);
    overflow: hidden;
    text-align: center;
    position: relative;
    transition: 0.4s;
    &::after {
        position: absolute;
        content: "";
        left: 0;
        width: 0;
        height: 2px;
        background: var(--color-primary);
        transition: 0.4s;
        bottom: 0;
    }
    .inner {
        .thumbnail {
            a {
                img {
                    width: 100%;
                }
            }
        }
        .content {
            padding: 30px;

            @media #{$lg-layout} {
                padding: 25px 20px;
            }

            @media #{$md-layout} {
                padding: 25px 20px;
            }

            @media #{$sm-layout} {
                padding: 25px 20px;
            }

            .course-total {
                font-weight: 500;
                font-size: 16px;
                line-height: 26px;
                display: inline-block;
                margin-bottom: 5px;
                color: var(--color-primary);
            }
            .title {
                margin-bottom: 20px;
                
                @media #{$lg-layout} {
                    margin-bottom: 6px;
                }

                @media #{$md-layout} {
                    margin-bottom: 6px;
                }

                @media #{$sm-layout} {
                    margin-bottom: 6px;
                }
            }
            .description {
                margin-bottom: 0;
            }
        }
        .program-info {
            .program-info-content {
                border-top: 1px solid #000;
                border-bottom: 1px solid #000;
                span {
                    margin-top: 5px;
                }
            }
        }
    }
    &:hover {
        transform: translateY(-15px);
        transition-delay: 0.3s;
        &::after {
            width: 100%;
        }
    }
}

.research-projects {
    height: 100%;
    .inner {
        .thumbnail {
            overflow: hidden;
            img {
                transition-duration: 0.3s;
            }
        }
    }
}

.research-projects:hover {
    img {
        transform: scale(1.2);
    }
}

/*----------------------
    Service Style Two  
-----------------------*/
.service-card-2 {
    padding: 40px 30px;
    background: #EAF8F6;
    border-radius: 5px;
    
    @media #{$lg-layout} {
        padding: 40px 20px;
    }
    
    @media #{$md-layout} {
        padding: 40px 20px;
    }
    
    @media #{$sm-layout} {
        padding: 40px 20px;
    }
    .inner {
        text-align: center;
        .icon {
            position: relative;
            display: inline-block;
            a {
                width: 80px;
                height: 80px;
                background: var(--color-white);
                line-height: 80px;
                text-align: center;
                border-radius: 100%;
                margin: 0 auto;
                display: block;
            }
            img {
                
            }
            .shape-list {
                .shape {
                    position: absolute;
                    z-index: 2;
                    &.shape-1 {
                        bottom: 0;
                        right: 0;
                        transition: 0.4s;
                        opacity: 0;
                    }
                    &.shape-2 {
                        left: 0;
                        top: 50%;
                        transition: 0.4s;
                        opacity: 0;
                    }
                    &.shape-3 {
                        top: 0;
                        right: 0;
                        transition: 0.4s;
                        opacity: 0;
                    }
                }
            }
        }
        .content {
            .title {
                font-size: 20px;
                line-height: 32px;
                margin-bottom: 15px;
                margin-top: 25px;
            }
            .description {
                margin-bottom: 0;
            }
        }
    }
    
    &.card-bg-2 {
        background: #FFF3EE;
    }
    &.card-bg-3 {
        background: #EFF4FC;
    }
    &.card-bg-4 {
        background: #FFF1F1;
    }

    &:hover {
        .inner {
            .icon {
                .shape-list {
                    .shape {
                        &.shape-1 {
                            bottom: -8px;
                            right: 3px;
                            opacity: 1;
                        }
                        &.shape-2 {
                            left: -20px;
                            top: 50%;
                            opacity: 1;
                        }
                        &.shape-3 {
                            top: -5px;
                            right: -1px;
                            opacity: 1;
                        }
                    }
                }
            }
        }
    }
}

