/*-------------------------------
    Service Style Four  
---------------------------------*/

.service-card-4 {
    height: 100%;
    .inner {
        background: #F5F5F5;
        border-radius: 5px;
        padding: 40px 35px;
        text-align: center;
        transition: 0.4s;
        height: 100%;

        @media #{$lg-layout} {
            padding: 25px 14px;
        }

        @media #{$md-layout} {
            padding: 25px 14px;
        }

        @media #{$sm-layout} {
            padding: 25px 14px;
        }

        .icon {
            position: relative;
            i {
                display: block;
                color: var(--color-primary);
                font-size: 35px;
            }
            .subtitle {
                opacity: 0;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                transition: 0.4s;
            }
        }
        .content {
            .title {
                font-weight: 700;
                font-size: 20px;
                line-height: 32px;
                margin-bottom: 15px;
                margin-top: 25px;
                a {
                    transition: 0.4s;
                    color: var(--color-heading);
                }
            }
            .description {
                margin-bottom: 0;
                transition: 0.4s;
            }
        }
    }
    &:hover {
        .inner {
            background: var(--color-primary);
            .icon {
                i {
                    opacity: 0;
                }
                .subtitle {
                    opacity: 1;
                    color: var(--color-white);
                }
            }
            .content {
                .title {
                    a {
                        color: var(--color-white);
                    }
                }
                .description {
                    color: var(--color-white);
                }
            }
        }
    }
}


/*-------------------------------
    Service Style Five  
---------------------------------*/
.service-card-5 {
    .inner {
        background: #F5F5F5;
        border-radius: 5px;
        padding: 20px;
        transition: 0.4s;
        display: flex;
        align-items: center;
        .icon {
            position: relative;
            width: 60px;
            height: 60px;
            display: flex;
            align-items: center;
            background: #FFFFFF;
            box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.02);
            border-radius: 2px;
            transition: 0.4s;
            justify-content: center;
            margin-right: 20px;
            @media #{$lg-layout} {
                margin-right: 15px;
            }
            i {
                color: var(--color-primary);
                font-size: 30px;
                transition: 0.4s;
            }
        }
        .content {
            .title {
                font-weight: 700;
                font-size: 20px;
                line-height: 32px;
                margin-bottom: 0;
                transition: 0.4s;
                @media #{$lg-layout} {
                    font-size: 17px;
                    line-height: 28px;
                }
            }
            .description {
                margin-bottom: 0;
                font-weight: 600;
                font-size: 16px;
                line-height: 26px;
                transition: 0.4s;
            }
        }
    }
    &:hover {
        .inner {
            background: var(--color-primary);
            .icon {
                box-shadow: none;
                background: rgba(255,255,255,0.15);
                i {
                    color: var(--color-white);
                }
            }
            .content {
                .title {
                    a {
                        color: var(--color-white);
                    }
                }
                .description {
                    color: var(--color-white);
                }
            }
        }
    }
}

