.service-card-single {
    position: relative;
    &:first-child {
        &::after {
            background-image: url(../../images/shape/arrow-down.png);
            content: "";
            width: 155px;
            height: 43px;
            right: -19%;
            top: 40px;
            background-size: cover;
            background-position: center center;
            position: absolute;
            @media #{$md-layout} {
                display: none;
            }
            @media #{$sm-layout} {
                display: none;
            }
        }
    }
    &:last-child {
        &::after {
            background-image: url(../../images/shape/arrow-top.png);
            content: "";
            width: 155px;
            height: 43px;
            left: -19%;
            top: 0;
            background-size: cover;
            background-position: center center;
            position: absolute;
            @media #{$md-layout} {
                display: none;
            }
            @media #{$sm-layout} {
                display: none;
            }
        }
    }
}
