/* tabs */
$transition: all 0.6s ease;
.industries-list-tab {
    .tabs {
        list-style-type: none;
        padding: 0;
        margin: 0;

        li {
            position: relative;
            background-color: #fff;
            box-shadow: rgba(0,0,0,0.8);
            border-radius: 60px 0 0 60px;
            transition: $transition;
            overflow: hidden;
            z-index: 1;
            cursor: pointer;
            &:last-child {
                margin-bottom: 0;
            }
            span {
                display: inline-block;
                padding: {
                    top: 4px;
                    left: 28px;
                    right: 8px;
                    bottom: 4px;
                }
                h3 {
                    margin: 0;
                }
            }
            &::before {
                content:  "";
                position: absolute;
                top: 0;
                left: 0;
                width: 0;
                height: 100%;
                background-color: var(--color-primary);
                z-index: -1;
                border-radius: 60px 0 0 60px;
                transition: $transition;
            }
            h3 {
                transition: $transition;
                font-size: 20px;
            }
            p {
                margin-bottom: 0;
                display: block;
                transition: $transition;
                // color: $body-color;
            }
            &:active {
                h3 {
                    color: #fff;
                }
                p {
                    color: #fff;
                }
                a {
                    right: 20px;
                }
                &::before {
                    width: 100%;
                    border-radius: 60px 0 0 60px;
                }
            }
            &.current {
                i {
                    color: #fff;
                    transform: rotate(360deg);
                }
                h3 {
                    color: #fff;
                }
                p {
                    color: #fff;
                }
                a {
                    right: 20px;
                }
                &::before {
                    width: 100%;
                    border-radius: 60px 0 0 60px;
                }
            }
        }
    }
}
