// Jobs page
.jobs-accordion {
    .accordion {
        list-style-type: none;
        padding-left: 0;
        margin-bottom: 0;
        border: none;

        .accordion__item {
            margin-bottom: 15px;
            border: none;

            &:last-child {
                margin-bottom: 0;
            }
        }
        .accordion__button {
            border-radius: 5px;
            display: block;
            box-shadow: 2px 8px 20px 0 rgba(25, 42, 70, 0.13) !important;
            background: #ffffff;
            padding: 20px 25px;
            color: var(--color-heading);
            text-decoration: none;
            position: relative;
            display: block;
            font-size: 18px;
            font-weight: 600;

            &:focus {
                outline: 0;
            }
            
            i {
                position: absolute;
                right: 25px;
                top: 18px;
                font-size: 25px;
                transition: 0.5s;
            }
        }
        .accordion-title.active {
            i {
                transform: rotate(180deg);
            }
        }
        .accordion__panel {
            background-color: var(--color-white);
            position: relative;
            padding-bottom: 20px;
            padding-right: 60px;
            padding-left: 25px;
            box-shadow: 2px 8px 20px 0 rgba(25, 42, 70, 0.13) !important;
            p {
                line-height: 1.8;
            }
        }
        .accordion-content.show {
            display: block;
        }
    }
}

